        get_date:function($day){
            var today = new Date();
            var oneday = 1000 * 60 * 60 * 24*$day;
            var begindate;
            begindate=new Date(today - oneday);
            var y=begindate.getFullYear(); //(4,1970-????)
            var m=begindate.getMonth()+1; //(0-11,01)
            var d=begindate.getDate(); //(1-31)
            return y+"-"+m+"-"+d;
        },
        // 
        getCookie: function(cname){
            var name = cname + "=";
            var ca = document.cookie.split(';');
            for(var i=0; i<ca.length; i++) {
                var c = ca[i].trim();
                if (c.indexOf(name)==0) { return c.substring(name.length,c.length); }
            }
            return "";
        },
